import { Component, h, Host } from '@stencil/core';
import { Prop } from '@stencil/core/internal';

@Component({
  tag: 'soul-skeleton-table',
  styleUrl: 'soul-skeleton-table.scss',
  shadow: true,
})
export class SoulSkeletonTable {
  private defaultRowsNumber = 3;
  private defaultColumnsNumber = 2;

  @Prop({ reflect: true })
  public withHeader: boolean = true;

  @Prop({ reflect: true })
  public rows = this.defaultRowsNumber;

  @Prop({ reflect: true })
  public columns = this.defaultColumnsNumber;

  getColumns(): number {
    return this.columns < 0 ? this.defaultColumnsNumber : this.columns;
  }

  getRows(): number {
    return this.rows < 0 ? this.defaultRowsNumber : this.rows;
  }

  render() {
    return (
      <Host>
        <table class="soul-table">
          {this.withHeader ?
            <thead>
              <tr>
                {[...Array(this.getColumns())].map(() => <th class="soul-table-cell"><soul-skeleton-label></soul-skeleton-label></th>)}
              </tr>
            </thead> : ''}
          <tbody>
            {[...Array(this.getRows())].map(() => <tr class="soul-table-row">
              {[...Array(this.getColumns())].map(() => <td class="soul-table-cell"><soul-skeleton-text lines={1}></soul-skeleton-text></td>)}
            </tr>)}
          </tbody>
        </table>
      </Host >
    );
  }
}
